div a {
    text-decoration: none;
}

a.avatar {
    position: fixed;
    bottom: 15px;
    right: -100px;
    animation: slide 0.5s 4.5s forwards;
    display: block;
    z-index: 4;
}

a.avatar img {
    border-radius: 100%;
    width: 44px;
    border: 2px solid #fff;
}

@keyframes slide {
    from {
        right: -100px;
        transform: rotate(360deg);
        opacity: 0;
    }

    to {
        right: 15px;
        transform: rotate(0);
        opacity: 1;
    }
}